// 後台的刪除modal
<template>
  <div class="modal" tabindex="-1" ref="DeleteModal">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header bg-danger">
          <h5 class="modal-title text-white">確認刪除</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <p>是否確認刪除(刪除後無法復原)</p>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            取消
          </button>
          <button type="button" class="btn btn-danger" @click="$emit('deleteSomething')">確認刪除</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal'
export default {
  data () {
    return {
      model: {} // 宣告modal實體
    }
  },
  props: ['product'],
  methods: {
    // 建立deletemodal方法
    showDeleteModal () {
      this.modal.show()
    },
    hideDeleteModal () {
      this.modal.hide()
    }
  },
  // 建立modal實體
  mounted () {
    this.modal = new Modal(this.$refs.DeleteModal)
  }
}
</script>
